@import url(https://fonts.googleapis.com/css?family=Anton);
@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900);
/* App css */

.Image-section {
  background-image: url(/static/media/bglanding.9d28b0cc.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  max-width: 100%;
}

.Jumbotron {
  padding-top: 2rem;
  height: 350px;
  background-image: url(/static/media/bgstories.26ac4a72.jpg);
  background-size: cover;
  overflow: hidden;
}
.list-ic li {
  position: relative;
}
.list-ic li span {
  display: inline-block;
  font-weight: 800;
  width: 1em;
  height: 1em;
  text-align: center;
  line-height: 3em;
  border-radius: 50%;
  background: #18bc9c;
  color: white;
  position: relative;
}
.list-ic li::before {
  content: '';
  position: absolute;
  background: black;
  z-index: -1;
  opacity: 0.1;
}
.list-ic.vertical {
  padding: 0;
  margin: 0;
}
.list-ic.vertical li {
  list-style-type: none;
  text-align: left;
}
.list-ic.vertical li::before {
  top: -60px;
  left: 31px;
  width: 0.1em;
  height: 8em;
}

.card {
  display: inline-block;
  position: relative;
  background: pink;
  padding: 15px;
  padding-left: 30px;
  margin-left: 30px;
  margin-left: 3rem;
  height: 80px;
  width: 300px;
  border-radius: none;
}
.card:after {
  content: '';
  display: block;
  position: absolute;
  right: 100%;
  top: 50%;
  margin-top: -30px;
  width: 0;
  height: 0;
  border-top: 30px solid transparent;
  border-right: 30px solid pink;
  border-bottom: 30px solid transparent;
  border-left: 30px solid transparent;
}

.Email-footerInput input {
  width: 100%;
  background-color: #fff;
  border: 1px solid #fff;
  text-align: center;
  height: 2rem;
  font-size: 15px;
}

html {
  min-height: 100%;
}

body {
  padding: 0;
  margin: 0;
  min-height: 100%;
  display: inline-block;
  background-color: white;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: 'Montserrat', sans-serif;
}

